<template>
    <div class="top">
        <div class="btnsView" v-for="(item,section) in banners" :key="section">
            <img :src="getImageName(section +  1)">
            <p>{{item}}</p>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'HomeTop',
        data() {
            return {
                msg: '首页',
                banners: ["详情", "分享", "扫一扫", "定位", "小程序", "添加桌面", "离线包", 'Vue学习']
            };
        },
        methods: {
            mpClink(operationType, params) {

                window.AlipayJSBridge.call('umapJsApis', {
                    operationType: operationType,
                    params: params
                }, function (result) {
                    console.log(result);
                });
            },
            openNewPage(urlStr) {
                // 打开淘宝首页，自动读取 title，并且去除右边菜单按钮
                window.AlipayJSBridge.call('pushWindow', {
                    url: urlStr,   // 要打开页面的 URL
                    // 打开页面的配置参数
                    param: {
                        readTitle: true,              //自动读取 title
                        showOptionMenu: false         // 隐藏右边菜单
                    },
                    // 用于给新开的页面传递参数，可选
                    // 在新开的页面使用 AlipayJSBridge.startupParams 可以获取到 passData
                });
            },
            getImageName(idex) {
                return require("@/assets/menus/0" + idex + ".png")
            },
            itemClink(indx) {
                switch (indx) {
                    case 0:
                        this.openNewPage('https://houhout.github.io/testCode/testCode.html');
                        break;
                    case 1:
                        this.mpClink('shareMore');
                        break;
                    case 2:
                        this.mpClink('scanCode');
                        break;
                    case 3:
                        this.mpClink('addresss');
                        break;
                    case 4:
                        this.mpClink('tiny', { 'appId': '2021003186692012' });
                        break;
                    case 5:
                        this.mpClink('addDeskTop', { 'url': 'https://houhout.github.io/iOS/appStart.html?appName=哈哈哈&appIcon=https://houhout.github.io/img/seaworldLogo.jpg' });
                        break;
                    case 6:
                        this.mpClink('startAppId', { 'appId': '20230000' });
                        break;
                    case 7:

                        this.mpClink('startAppUrl', { 'url': 'https://v2.cn.vuejs.org/' })
                }
            }
        }
    }

</script>

