    .top {
        height: 230px;
        /* background-image: image-set('@/assets/topBg.jpg');
         */
        background-color: whitesmoke;
        columns: auto;
        columns: 4;
        padding: 110px 10px 10px;

    }

    .btnsView {

        height: 95px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    p {
        margin-top: 5px;
    }
</style>