<template>
    <div class="contt">
        <img class="adbannner" :src="imgStr" />
    </div>
</template>

<script>
    import axios from "axios";

    export default {
        name: 'HomeContent',
        data() {
            return {
                imgStr: ''
            };
        },
        mounted() {
            axios.get('https://api.oioweb.cn/api/common/OneDayEnglish').then(res => {
                //请求成功，触发then中的函数
                this.imgStr = res.data['result']['img']
            }).catch(error =>
                //请求失败，触发catch中的函数 可省略
                console.log(error)
            )
        },
    }
</script>

<style scoped>
    .contt {
        width: 100%;
        /* height: 600px; */
        display: flex;
        flex-direction: column;
    }

    .adbannner {
        margin: 10px;

    }
</style>